import { createStore } from 'vuex';

const store = createStore({
    state: {
        options: [
            {
                id: 'head',
                title:'Headline',
                image: './image/Headline.svg',                
            },
            {
                id: 'paragraph',
                title:'Paragraph',
                image: './image/text-align-left.svg'
            },
            {
                id: 'button',
                title:'Button',
                image: './image/image.svg'
            },
            {
                id: 'image',
                title:'Image',
                image: './image/image-2.svg'
            },
        ],
        optionsMain: [],
        valueInputs: [],
        targetItemId:''
    },
    mutations:{
        overId(state, id){
            if(id) {
                state.targetItemId = id;
            }
        },
        valueInput(state, payload) {
            const item = state.valueInputs.findIndex(res => res.id === payload.id);
            if(item < 0) {
                const newItem = {
                    id: payload.id,
                    title: payload.title,
                    value: payload.value
                }
                state.valueInputs.push(newItem);
            } else {
                state.valueInputs.forEach(res => {
                            if(res.id === payload.id) {
                                res.value = payload.value
                            }
                        } 
                    )
            }
            console.log(state.valueInputs)
        },        
        dropItem (state, payload) {
            const item = state.options.findIndex(res => res.id === payload);
            const newItem = {
                id: new Date().toISOString(),
                title: state.options[item].title,
                image: state.options[item].image,
                isFocus: false,
            }  
            if(state.targetItemId){
                const index = state.optionsMain.findIndex(res => res.id === state.targetItemId);              
                state.optionsMain.splice(index, 0, newItem);
                state.targetItemId='';
            } else {
                state.optionsMain.unshift(newItem);
            }      
            
        },
        isFocus (state, id) {
            const optionsItem = state.optionsMain.findIndex(res => res.id === id);
            state.optionsMain.forEach(item => item.isFocus = false)      
            state.optionsMain[optionsItem].isFocus = !state.optionsMain[optionsItem].isFocus
        },
        itemUp (state,id) {
            const index = state.optionsMain.findIndex(res => res.id === id);
            const arrItem = state.optionsMain[index]
            if(index != 0) {
                state.optionsMain.splice(index, 1);
                state.optionsMain.splice(index - 1, 0, arrItem)
            }
        },
        itemCopy(state,id) {
            const index = state.optionsMain.findIndex(res => res.id === id);
            const newItem = {
              id: new Date().toISOString(),
              title: state.optionsMain[index].title,
              image: state.optionsMain[index].image,
              isFocus: false,
            }
            state.optionsMain.splice(index + 1, 0,newItem)
        },
        itemDown (state, id) {
            const index = state.optionsMain.findIndex(res => res.id === id);
            const arrItem = state.optionsMain[index]
            if(index != state.optionsMain.length) {
                state.optionsMain.splice(index, 1);
                state.optionsMain.splice(index + 1, 0, arrItem)
            }
        },
        itemDelete(state, id) {
            const index = state.optionsMain.findIndex(res => res.id === id);
            const indexVal = state.valueInputs.findIndex(res => res.id === id);      
            state.optionsMain.splice(index, 1);
            state.valueInputs.splice(indexVal, 1);
        },
        dragItem(_, payload) {    
            payload.evt.dataTransfer.dropEffect = 'move';
            payload.evt.dataTransfer.dropAllowed = 'move';
            payload.evt.dataTransfer.setData('itemId', payload.id);
        }, 
    },
    getters: {
        optionsList (state) {
            return state.options
        },
        optionsMainList (state) {
            return state.optionsMain
        },
        valueInputsList( state) {
            return state.valueInputs
        }

    }
})

export default store;