<template>
   <the-header></the-header>
   <div class="main-wrapper">
      <the-aside>
        <options-list :isSidebar="true">
            <option-item v-for="option in optionsList" 
              :key='option.id'
              :id="option.id"
              :title="option.title"
              :image="option.image" 
              draggable='true'
              ></option-item> 
        </options-list>
      </the-aside>
      <the-main>
        <options-list @drop="odDrop" @dragenter.prevent @dragover.prevent='onOver' >
          <option-item-main v-for="option in optionsMainList" 
            :key='option.id'
            :id="option.id"
            :title="option.title"
            :image="option.image"
            :isFocus="option.isFocus"
            draggable='true'
            ></option-item-main>
          </options-list>
      </the-main>      
   </div>
</template>

<script>
import TheHeader from './components/layouts/TheHeader.vue'
import TheAside from './components/layouts/asside.vue'
import TheMain from './components/layouts/Main.vue'
import OptionsList from './components/options/OptionsList.vue'
import OptionItem from './components/options/OptionsItem.vue'
import OptionItemMain from './components/options/OptionsItemMain.vue';
import { mapGetters } from 'vuex';
export default {
  name: 'App',
  components:{
    TheHeader,
    TheAside,
    TheMain,
    OptionsList,
    OptionItem,
    OptionItemMain
  },
  methods: {   
    odDrop(e) { 
      const itemId =  e.dataTransfer.getData('itemId');
      this.$store.commit('dropItem', itemId)
    },
    onOver(e) {
      this.$store.commit('overId', e.target.__vueParentComponent.props.id);
    }
  },
  computed: {
      ...mapGetters([
        'optionsList',
        'optionsMainList'
      ])
  }
}
</script>

